import * as React from "react"
import { Link, graphql } from "gatsby"

import Nav from "../components/nav"
import Layout from "../components/layout"
import Seo from "../components/seo"
import Toc from "../components/toc"
import { useEffect } from "react"

const BlogPostTemplate = ({
  data: { previous, next, site, markdownRemark: post, allMarkdownRemark },
  location,
}) => {
  const siteTitle = site.siteMetadata?.title || `Title`
  const tocdata = post.tableOfContents
  const categories = allMarkdownRemark.group
  const totcount = allMarkdownRemark.totalCount
  const headings = post.headings
  let observers = []
  const heads = {}
  headings.forEach((element, index) => {
    heads[element.id] = index
    observers.push(new IntersectionObserver((entries) => {
      const interIndex = heads[entries[0].target.id]
      const lis = document.getElementsByClassName('tocnav')[0].getElementsByTagName("a")
      if (entries[0].isIntersecting) {
        console.log(entries[0].target.id, "나타남", entries[0])
      }
      else {
        console.log(entries[0].target.id, "사라짐", entries[0])
      }
    },
    {
      rootMargin: "200px 0px 0px 0px"
    }
  ))
  });
  
  // 옵저버 등록
  useEffect(()=> {
    observers.forEach((observer, index) => {
      observer.observe(document.getElementById(headings[index].id))
    })
    const lis0 = document.getElementsByClassName('tocnav')[0].getElementsByTagName("a")
    Array.from(lis0).forEach((element) => {
      element.style.fontWeight = "500"
    })
  }, [])
  



  return (
    <Layout location={location} title={siteTitle}>
      <Nav 
      title={post.frontmatter.title} 
      date={post.frontmatter.date} 
      categories={categories} 
      count={totcount} 
      location={post.frontmatter.category}
      />
      <div className="blogpostwrapper">
        <div className="post-toc-wrapper">
          <div
            className="blog-post"
            itemScope
            itemType="http://schema.org/Article"
          >
            <header>
              <h1 itemProp="headline">{post.frontmatter.title}</h1>
              <p>{post.frontmatter.date}</p>
            </header>
              <section
                dangerouslySetInnerHTML={{ __html: post.html }}
                itemProp="articleBody"
              />
            <footer>
            </footer>
          </div>
          <Toc data={tocdata}/>
        </div>
        <nav className="blog-post-nav">
          <ul
            style={{
              display: `flex`,
              flexWrap: `wrap`,
              justifyContent: `space-between`,
              listStyle: `none`,
              padding: 0,
            }}
          >
            <li>
              {previous && (
                <Link to={previous.fields.slug} rel="prev">
                  ← {previous.frontmatter.title}
                </Link>
              )}
            </li>
            <li>
              {next && (
                <Link to={next.fields.slug} rel="next">
                  {next.frontmatter.title} →
                </Link>
              )}
            </li>
          </ul>
        </nav>
      </div>
    </Layout>
  )
}

export const Head = ({ data: { markdownRemark: post } }) => {
  return (
    <Seo
      title={post.frontmatter.title}
      description={post.frontmatter.description || post.excerpt}
    />
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) 
  {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      html
      tableOfContents
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        category
      }
      headings {
        id
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    allMarkdownRemark(sort: { frontmatter: { date: DESC } }) {
      totalCount
      group(field: { frontmatter: { category: SELECT }}) {
        fieldValue
        totalCount
      }
    }
  }
`
